@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  @apply font-main text-black;
}

@layer components {
  .h1 {
    @apply font-bold text-[3.75rem];
  }

  .h2 {
    @apply font-bold text-[2rem];
  }

  .h3 {
    @apply font-bold text-[1.5rem];
  }

  .subtitle {
    @apply font-semibold text-[1.25rem];
  }

  .subtitle2 {
    @apply font-semibold text-[1rem];
  }

  .bodyText {
    @apply font-medium text-[1.25rem];
  }

  .bodyText2 {
    @apply font-medium text-[1rem];
  }

  .mob_h1 {
    @apply font-bold text-[1.5rem];
  }
  .mob_subtitle {
    @apply font-semibold text-[1.25rem];
  }
  .mob_subtitle2 {
    @apply font-semibold text-[0.875rem];
  }
  .mob_bodyText {
    @apply font-medium text-[0.875rem];
  }
}

.container {
  margin: 0 auto;
  @apply lg:w-[69.375rem] md:w-[44.625rem] sm:w-[18.125rem];
}
